<template>
  <div class="resume">
      <div> 
        <div class="weui-cell my_titel" aria-controls="">
            <div class="weui-cell__hd"><img class="my_pic" src="../../assets/img/my.jpg"/></div>
            <div class="weui-cell__bd">
                <p style="color:#999"></p>
            </div>
            <div class="weui-cell__ft" style="margin-right:33px;">{{this.allNmuber}}</div>
        </div>
      </div>
    <div v-if="test" style="padding-top:55px;padding-bottom:40px;">
      <div class="weui-cell weui-cell_access" v-for="item in items">
          <div class="weui-cell__bd" style="position: relative;">
              <span class="my_test" style="vertical-align: middle">{{item.test}}</span>
              <span class="weui-badge" style="margin-left: 5px;position: absolute;right: 10px;top:6px;">{{item.num}}</span>
          </div>
      </div>
    </div>  
    <div v-if="!test" style="background:#000;color:#fff;height:1000px;width:100%;padding:75px 35px 0 15px;">
      <div>
      <h4>1</h4>
      <p style="margin-bottom:15px;padding-right:20px"></p>
      <h4>2</h4>
      <p style="margin-bottom:15px;padding-right:20px"></p>
      <h4>3</h4>
      <p style="margin-bottom:15px"></p>
      <h4>4</h4>
      <p style="margin-bottom:15px;padding-right:20px"></p>
      <div class="weui-footer">
        <p class="weui-footer__links">
            <a href="javascript:void(0);" class="weui-footer__link"></a>
        </p>
        <p class="weui-footer__text">Copyright  2008-2016 weui.io</p>
      </div>
      </div>
    </div>
      <div class="footer" style="border-top:1px solid #ccc">
        <div class="weui-flex">
            <div class="weui-flex__item"><div class="placeholder" @click="showtest"></div></div>
            <div class="weui-flex__item"><div class="placeholder" @click="showogher"></div></div>
        </div>      
      </div>       
  </div>
</template>
<script>
export default {
  name: "resume",
  data() {
    return {
      isActive: "first",
      allNmuber: 0,
      test: true,
      items: [
        { test: "1:", num: 3 },
        { test: "2:", num: 1 },
        { test: "3:", num: 5 },
        {
          test: "4",
          num: 1
        },
        { test: "5:", num: 1 },
        { test: "6:", num: 5 },
        { test: "7:", num: 5 },
        { test: "8:", num: 5 },
        { test: "9:", num: 1 },
        { test: "10:", num: 1 },
        { test: "11:", num: 5 },
        { test: "12:", num: 5 },
        { test: "13:", num: 5 },
        { test: "14:", num: 1 },
        { test: "15:", num: 1 },
        { test: "16:", num: 5 },
        { test: "17:", num: 1 },
        { test: "18:", num: 5 },
        { test: "19:", num: 1 },
        { test: "20:", num: 1 },
        { test: "21:", num: 5 },
        { test: "22:", num: 5 },
        { test: "23:", num: 1 },
        { test: "24:", num: 1 },
        { test: "25:", num: 5 },
        { test: "26:", num: 5 },
        { test: "27:", num: 5 },
        { test: "28:", num: 5 },
        { test: "29:", num: 5 },
        { test: "30:", num: 5 },
        { test: "31:", num: 1 },
        { test: "32:", num: 1 },
        { test: "32:", num: 1 },
        { test: "33:", num: 5 },
        { test: "34:", num: 1 },
        { test: "35:", num: 5 },
        { test: "36:", num: 5 },
        {
          test:
            "37:",
          num: 1
        },
        { test: "38:", num: 5 },
        { test: "39:", num: 5 },
        {
          test:
            "40:",
          num: 5
        },
        { test: "41:", num: 1 },
        { test: "42:", num: 1 },
        { test: "43:", num: 1 },
        { test: "44:", num: 5 }
      ]
    };
  },
  methods: {
    showtest: function() {
      this.test = true;
    },
    showogher: function() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.test = false;
    }
  },
  mounted: function() {
    var localNnumber = 0;
    for (var i = 0; i < this.items.length; i++) {
      localNnumber += this.items[i].num;
      console.log(this.items[i].num);
    }
    this.allNmuber = localNnumber;
  }
};
</script>
<style>
.my_test {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  width: 300px;
}
.my_pic {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  margin-right: 15px;
}
.my_titel {
  position: fixed;
  top: 0px;
  background: #ffffff;
  width: 100%;
  z-index: 9999;
}
.footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: #fff;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  z-index: 9999;
}
</style>



